@font-face {
	font-family: Gilbert;
	font-weight: 700;
}

.wp-block-create-block-gutenpride {
	font-family: Gilbert, sans-serif;
	font-size: 64px;
}

.error404 .wp-site-blocks {
	padding-top: 0 !important; }

.section-page-404 {
	position: relative;
	padding: 7rem 0;
	min-height: 100%;
	width: 100%;
	display: flex;
	align-items: flex-start; }
@media (max-width: 1279.5px) and (orientation: landscape) {
	.section-page-404 {
		padding: 4rem 0; } }
@media (max-width: 1024.5px) and (orientation: landscape) {
	.section-page-404 {
		padding: 3rem 0; } }
.admin-bar .section-page-404 {
	min-height: calc((var(--vh, 1vh) * 100) - 32px); }
@media (max-width: 782px) {
	.admin-bar .section-page-404 {
		min-height: calc((var(--vh, 1vh) * 100) - 46px); } }
.section-page-404__bg:before {
	display: none; }
.section-page-404 .section__body {
	margin: auto;
	flex: 0 0 100%;
	max-width: 100%; }
.section-page-404 .section__body .section-page-404__content-wrapper {
	padding: 12rem 29.6rem;
	-webkit-backdrop-filter: blur(10px);
	backdrop-filter: blur(10px); }
@media (max-width: 1279.5px) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 8rem 10rem; } }
@media (max-width: 1279.5px) and (orientation: landscape) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 6rem 6.4rem; } }
@media (max-width: 1024.5px) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 9rem 6.4rem; } }
@media (max-width: 1024.5px) and (orientation: landscape) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 4rem 6.4rem; } }
@media (max-width: 767.5px) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 6rem 3rem; } }
@media (max-width: 767.5px) and (orientation: landscape) {
	.section-page-404 .section__body .section-page-404__content-wrapper {
		padding: 3rem; } }
.theme-dark .section-page-404 .section__body .section-page-404__content-wrapper {
	background-color: rgba(var(--color-black-RGB), 0.8);
	transition: background-color 0.3s ease-in-out; }
.theme-light .section-page-404 .section__body .section-page-404__content-wrapper {
	background-color: rgba(var(--color-white-RGB), 1);
	transition: color 0.3s ease-in-out; }
@supports not ((backdrop-filter: blur(10px)) or (-webkit-backdrop-filter: blur(10px))) {
	.theme-dark .section-page-404 .section__body .section-page-404__content-wrapper {
		background-color: rgba(var(--color-black-RGB), 0.97);
		transition: background-color 0.3s ease-in-out; }
	.theme-light .section-page-404 .section__body .section-page-404__content-wrapper {
		background-color: rgba(var(--color-white-RGB), 1);
		backdrop-filter: none;
		transition: color 0.3s ease-in-out; } }
.section-page-404 .section__body .section-page-404__content {
